.App {
  text-align: center;
}

* {
  margin: 0;
  padding: 0;
}

@font-face {
  font-family: Montserrat;
  src: local('Montserrat'),
    url(./fonts/Montserrat/Montserrat-VariableFont_wght.ttf) format('truetype');
}

body {
  background-image: url('./images/RingedSpicyKodiakbear\ \(1\).gif');
  color: rgba(242, 238, 245, 0.977);
  font-family: 'Montserrat';
}

header {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 100px;
  margin-bottom: 140px;
}

header h1 {
  color: rgb(242, 238, 245);
  font-size: 50px;
  font-weight: 500;
  text-transform: uppercase;
}

.ellipse {
  align-self: center;
  position: absolute;
  width: 120.07px;
  height: 307.45px;
  text-align: center;
  border-radius: 100%;
  transform: matrix(0.58, 0.78, -0.87, 0.56, 0, 0);
  border: rgba(247, 245, 248, 0.52) 2px solid;
}

.planets {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
}

.planets::after {
  content: '';
  width: 100%;
  height: 1px;
  background-color: rgb(242, 238, 245);
  position: absolute;
  z-index: -9999;
}

div[data-testid='solar-system'] h2 {
  text-align: center;
  margin-bottom: 40px;
  font-size: 33px;
  font-weight: 300;
  letter-spacing: 1px;
  text-transform: uppercase;
}

.card {
  display: flex;
  justify-content: center;
}

.name {
  position: absolute;
  bottom: 0;
  margin: 0;
  transform: translateY(100%);
  font-size: 16px;
}

img[src*='Mercurio'] {
  width: 41px;
}

img[src*='Venus'] {
  width: 102px;
}

img[src*='Terra'] {
  width: 106px;
}

img[src*='Marte'] {
  width: 66px;
}

img[src*='Jupiter'] {
  width: 272px;
}

img[src*='Saturno'] {
  width: 282.41px;
}

img[src*='Urano'] {
  width: 144px;
}

img[src*='Netuno'] {
  width: 144px;
}

img[src*='png'] {
  transition: transform 700ms;
}

img[src*='png']:hover {
  transform: translateY(-10px);
}

div[data-testid='missions'] {
  display: flex;
  gap: 20px;
  flex-wrap: wrap;
  justify-content: center;
  background-color: rgba(255, 255, 255, 0.098);
  margin: 75px 75px;
  padding-top: 100px;
  padding-bottom: 50px;
  border-radius: 20px;
}

div[data-testid='missions'] h2 {
  position: absolute;
  top: 700px;
  font-weight: 300;
  letter-spacing: 1.2px;
  text-transform: uppercase;
  font-size: 35px;
}

.separador {
  display: flex;
  width: 100%;
  height: 1px;
  background-color: white;
}

.mission-container {
  display: flex;
  background-color: rgba(24, 26, 37, 0.49);
  width: 496px;
  height: 180px;
  border-radius: 20px;
  border: 1px solid white;
  transition: 700ms;
}

.mission-container:hover {
  background-color: rgba(106, 109, 124, 0.281);
  transition: 500ms;
}

.dates {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  margin-top: 20px;
}

div[data-testid='mission-card'] {
  display: flex;
  flex-direction: column;
  width: 100%;
}

.name-mission {
  display: flex;
  font-size: 24px;
  text-transform: uppercase;
  padding: 20px;
  align-items: center;
  height: 50px;
}

.year-mission,
.country-mission,
.destiny {
  display: flex;
  flex-direction: row;
  gap: 5px;
  margin: 8px 20px;
}
